<template>
  <div class="readerinfo">
    <el-form ref="readerList" :rules="addRules" :model="readerList" label-width="80px" size="small">
  <el-form-item label="用户编号" prop="readerId">
    <el-input :disabled="true" v-model="readerList.readerId"></el-input>
  </el-form-item>
  <el-form-item label="真实姓名" prop="realName">
    <el-input v-model="readerList.realName"></el-input>
  </el-form-item>
  <el-form-item label="年龄"  prop="age">
    <el-input v-model="readerList.age"></el-input>
  </el-form-item>
  <el-form-item label="性别"  prop="sex">
     <el-radio v-model="readerList.sex" label="男" aria-checked="">男</el-radio>
    <el-radio v-model="readerList.sex" label="女">女</el-radio>
  </el-form-item>
  <el-form-item label="电话"  prop="phone">
     <el-input v-model="readerList.phone"></el-input>
  </el-form-item>
  <el-form-item label="当前信用等级"  prop="authority">
    <el-input :disabled="true" v-model="readerList.authority"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit">确定</el-button>
    <el-button  @click="cancel">取消</el-button>
  </el-form-item>
</el-form>
  </div>
</template>
<script>
import qs from 'qs';
  export default {
    
    mounted(){
        let m = sessionStorage.getItem("user")
    let session = JSON.parse(m);
                   this.axios.post('/reader/readerinfo',qs.stringify({readerId:session.userId}))
              .then(res => {
                console.log(res.data)
                    if (res.data) {
                      
                    this.readerList= res.data
                    }
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
    },
    data() {
      var validateNull = (rule, value, callback) => {
      if (value === '' || value===null) {
        callback(new Error('不能为空'));
      } else {
        callback();
      }
      };
      var validateNumber = (rule, value, callback) => {
        var numReg = /^[0-9]+$/
        var numRe = new RegExp(numReg)
      if (value === null || value ==='') {
        callback(new Error('不能为空'));
      } else if(value.length!=11 ){

        callback(new Error('必须为11位数字'));
      }else if (!numRe.test(value)) {
        callback("必须是数字")
      }else{
        callback();
      }
      };
      var validateNum = (rule, value, callback) => {
        var numReg = /^[0-9]+$/
        var numRe = new RegExp(numReg)
      if (value === '') {
        callback(new Error('不能为空'));
      } else if (!numRe.test(value)) {
        callback("必须是数字")
      }else if(parseInt(value)<0 || parseInt(value)>150){

        callback(new Error('数字越界'));
      }else{
        callback();
      }
      };
      return {
        readerList: {
                  readerId:'',
                  realName:'',
                  age:'',
                  sex:'',
                  phone:'',
                  authority:'',
        },
        addRules: {
        name: [
          { validator: validateNull, trigger: 'blur' }
        ],
        age: [
          { validator: validateNum, trigger: 'blur' }
        ],
        sex: [
          { validator: validateNull, trigger: 'blur' }
        ],
        phone: [
          { validator: validateNumber, trigger: 'blur' }
        ],
        level: [
          { validator: validateNull, trigger: 'blur' }
        ]
      }
      }
    },
    methods: {
      onSubmit() {
          this.$refs.readerList.validate((valid) => {
              let reader = {
                  readerId:this.readerList.readerId,
                  realName:this.readerList.realName,
                  age:this.readerList.age,
                  sex:this.readerList.sex,
                  phone:this.readerList.phone,
              }
              
              if (valid) {  
             this.axios.post('/reader/updatereader',reader)
              .then(res => {
                    if (res.data) {
                      if(res.data == 1){
                         this.$message.success(`修改成功`)
                      }
                      else{
                           this.$message.error(`修改失败`);
                      }
                    }
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
          }
          
        });
      
      },
      cancel(){
        this.$router.push('/book')
      },
  
    }
  }
</script>
















<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .readerinfo{
    width: 50%;
    height: 500px;
    margin: auto;
  }
  .el-form{
    margin-top: 50px;
  }
</style>
