<template>
  <div class="bookmanage" style="margin:auto">
    <b-container>
 <el-row :gutter="20">
  <el-col :span="6">
    <el-select v-model="condition" clearable placeholder="请选择搜索类型">
      <el-option label="作者" value="作者"></el-option>
      <el-option label="书名" value="书名"></el-option>
       <el-option label="出版社" value="出版社"></el-option>
      <el-option label="编号" value="编号"></el-option>
       <el-option label="类型" value="类型"></el-option>
    </el-select>
  </el-col>
  <el-col :span="6">   
    <el-input placeholder="请输入搜索内容" prefix-icon="el-icon-search" v-model="search">
  </el-input>
  </el-col>
  
  <el-col :span="2">
    
     <el-button type="primary" round size="medium" @click="applysearch" >搜索</el-button>  

  </el-col>
  <el-col :span="2">
    
     <el-button type="primary" round size="medium" @click="reset" >清空搜索</el-button>  

  </el-col>
   <!-- <el-col :span="80">
    
     <el-button type="primary" round size="medium" @click="addbook">新建图书</el-button>  

  </el-col> -->
</el-row>



    

     <el-table :data="this.pagedata.records" style="width: 100%;height:800"  max-height="800" @row-click="op">
       <!-- <el-table-column prop="bookId" label="图书编号" width="80"> </el-table-column> -->
       <el-table-column label="封面" width="80">
    　　<template slot-scope="scope">
    　　　　<img :src="'http://47.103.36.216:8085/bookimg/'+scope.row.picture" width="40" height="40" class="head_pic" />
    　　</template>
        </el-table-column>
      <el-table-column prop="bookName" label="书名" width="180"> </el-table-column>
      <el-table-column prop="ISBN" label="ISBN" width="160"> </el-table-column>
      <el-table-column prop="typeName" label="类型" width="100"> </el-table-column>
      <el-table-column prop="author" label="作者" width="100"> </el-table-column>
      <el-table-column prop="publisher" label="出版社" width="180"> </el-table-column>
          <el-table-column prop="shelter" label="书架" width="180"> </el-table-column>
      <el-table-column label="操作" width="180" v-if="isAdmin">
           <template slot-scope="scope">
         <el-button-group>
     <el-button type="primary" round size="mini" @click="modify(scope.row)">修改</el-button>  
      <!-- <el-button type="danger" round  size="mini" @click="dele(scope.row,scope.$index)">删除</el-button> -->
      
        </el-button-group>
           </template> 
      </el-table-column>
    
     
    </el-table><el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10]"
                            :page-size="pagesize"        
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="pagedata.total">  
                    </el-pagination>






    </b-container>
 
  </div>
</template>

<script>
import qs from 'qs';
export default {
  
  data(){
     return {
                currentPage:1, //初始页
                pagesize:10,    //    每页的数据
                pagedata:{
                  "records": [],
                   "total": 0,
                    "size": 0,
                    "current": 0,
                    "searchCount": true,
                    "pages":0
                },
                bookList:[],
                search:'',
                postBook:{
                  bookId:'',
                  bookName:'',
                  ISBN:'',
                  typeName:'',
                  author:'',
                  publisher:''
                },
                condition:"",
                onSearch:false,
                isAdmin:false,
              
      }
  },
  created() {
        this.handleBookList()
          if(sessionStorage.getItem('user')!=null){
        let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
          if(session.authority == 99){
             this.isAdmin = true;
          }
       }
  },
  methods: {
      op(row){
               this.$router.push({name:'detail',query:{info:row.bookId}});
      },
       
      // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
                if(!this.onSearch)
                this.handleBookList()
                else
                this.applysearch()
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage; 
                console.log(this.currentPage)  //点击第几页
                if(!this.onSearch)
                this.handleBookList()
                else
                this.applysearch()
        },
        handleBookList() {
              this.axios
            .post('/book/bookpage',qs.stringify({cu:this.currentPage,size:this.pagesize}))
            .then(response => (
              this.pagedata = response.data ,
              console.log(this.pagedata)
              ) )
        },
        dele(row,index){
          this.$confirm('确认删除？')
          .then(_ => {
              this.axios.post('SpringMVC/api/deletebook',row)
                    .then(res => {
                          if (res.data) {
                            if(res.data.success){
                              //删除
                              this.bookList.slice(row.index)
                              this.$message.success(`删除成功`)
                            }
                            else{
                              //删除失败
                                  this.$message.error(`删除失败`);
                            }
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
              console.log(index)
               this.bookList.splice(index,1)
          })
          .catch(_ => {});
          
        },
        applysearch(){
          if(this.search =='' || this.condition ==''){
             this.$message.error(`搜索条件不能为空`);
             return;
          }
          let bookData = {
              bookId:null,
              bookName:null,
              typeName:null,
              author:null,
              publisher:null
          }; 
          switch(this.condition){
            case '书名':
            bookData.bookName = this.search;
            break;
            case '作者':
            bookData.author = this.search;
            break;
            case '编号':
            bookData.bookId = this.search;
            break;
            case '类型':
            bookData.typeName = this.search;
            break;
            case '出版社':
            bookData.publisher = this.search;
            break;
            default:
          }
          if(!this.onSearchonSearch)
          {
            this.onSearchonSearch = true;
            this.currentPage = 1;
          }
          let up = {
             book:bookData,
             cu:this.currentPage,
             size:this.pagesize
          }
           this.axios.post('book/searchBook',up
        
        )
           
              .then(res => {
            this.bookList = res.data
                    if (res.data) {
                      console.log(res.data)
                      if(res.data){
                        this.pagedata=res.data
                        
                      }
                      else{
                        this.$message.error(`未找到结果`);
                      }
                    }
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
        },
        addbook(){
            this.$router.push('/bookchange');
        },
        reset(){
          this.onSearch = false;
          this.currentPage = 1;
          this.handleBookList()
          this.search=''
          this.condition=''
        },
        modify(row){
            this.$router.push({name:'modifybook',params:{info:row}});
        }
        
    }
}
</script>

<style scoped>
  .bookmanage{
    height: 500px;
  }
   .el-row {
    margin-bottom: 20px;
    margin-top: 20px;
   
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


