<template>
  <div class="bookmanage">
    <b-container>
  <el-row :gutter="20">
  <h4>我的收藏</h4>
</el-row>
     <el-table :data="bookList.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width: 100%"  max-height="350"  @row-click="op" >
    <el-table-column label="封面" width="80">
    　　<template slot-scope="scope">
    　　　　<img :src="scope.row.picture" width="40" height="40" class="head_pic" />
    　　</template>
        </el-table-column>
      <el-table-column prop="bookName" label="书名" width="200"> </el-table-column>
      <el-table-column prop="ISBN" label="ISBN" width="200"> </el-table-column>
      <el-table-column prop="typeName" label="类型" width="200"> </el-table-column>
      <el-table-column prop="author" label="作者" width="200"> </el-table-column>
      <el-table-column prop="publisher" label="出版社" > </el-table-column>
    
     
    </el-table><el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10]"
                            :page-size="pagesize"        
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="bookList.length">  
                    </el-pagination>
                    </b-container>
  </div>
</template>

<script>
import qs from 'qs';
export default {

  data(){
     return {
                currentPage:1, //初始页
                pagesize:10,    //    每页的数据
                bookList: [],
                search:'',
                postBook:{
                  bookId:'',
                  bookName:'',
                  ISBN:'',
                  typeName:'',
                  author:'',
                  publisher:''
                },
                condition:""
              
      }
  },
  created() {
        this.handleBookList()
       
  },
  methods: {
         op(row){
               this.$router.push({name:'detail',query:{info:row.bookId}});
      },

      // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },
        handleBookList() {
            if(sessionStorage.getItem('user')!=null){
        let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
         this.axios.post('favor/getfavor',qs.stringify({readerId:session.userId})
        
        )   
              .then(res => {

                    if (res.data) {
                      console.log(res.data)  
                      this.bookList = res.data
                    }
                      else{
                        this.$message.error(`加载失败`);
                      }
                    
                })
                .catch(err => {
                    this.$message.error(`错误`, 'ERROR!')
                })
        
       }

        },

        
    }
}
</script>

<style scoped>
  .bookmanage{
    height: 500px;
  }
   .el-row {
    margin-bottom: 20px;
    margin-top: 20px;
   
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


