<template>
  <div class="readermanage">
    <b-container>
          <el-row :gutter="20">
 <h4>用户管理</h4>
</el-row>
  <el-row :gutter="20" >
  <el-col :span="6">
<el-radio v-model="radio" label="1">用户编号</el-radio>
  <el-radio v-model="radio" label="2">用户名</el-radio>
  </el-col>
  <el-col :span="6">   
    <el-input placeholder="请输入搜索内容" prefix-icon="el-icon-search" v-model="search">
  </el-input>
  </el-col>
  
  <el-col :span="100">
    
     <el-button type="primary" round size="medium" @click="applysearch" >搜索</el-button>  

  </el-col>
  <el-col :span="6">
    
     <el-button type="primary" round size="medium" @click="reset" >清空搜索</el-button>  

  </el-col>
</el-row>



    

     <el-table :data="userList.slice((currentPage-1)*pagesize,currentPage*pagesize)" style=" width: 100%;margin: auto;"  max-height="350">
       <el-table-column prop="userId" label="读者编号" width="80"> </el-table-column>
      <el-table-column prop="userName" label="用户名" width="180"> </el-table-column>
        <el-table-column prop="authority" label="当前权限" width="180"> </el-table-column>
      <el-table-column label="操作" width="280">
           <template slot-scope="scope">
         <el-button-group>
     <el-button type="primary" round size="mini" @click="modify(scope.row,scope.$index)">赋予管理员权限</el-button>  
      <el-button type="danger" round  size="mini" @click="dele(scope.row,scope.$index)">删除</el-button>
      
        </el-button-group>
           </template> 
      </el-table-column>
    
     
    </el-table ><el-pagination
                            style=" width: 60%;margin: auto;"
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5]"
                            :page-size="pagesize"        
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="userList.length">  
                    </el-pagination>



    </b-container>

  </div>
</template>

<script>
export default {
        inject:['reload'],
  data(){
     return {
                currentPage:1, //初始页
                pagesize:5,    //    每页的数据
                userList: [],
                search:'',
                 radio: '1'
              
      }
  },
  created() {
        this.handleUserList()
       
  },
  methods: {
      // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },
        handleUserList() {
            this.axios
            .get('user/showuser')
            .then(response => (this.userList = response.data) )

        },
        dele(row,index){
          this.$confirm('确认删除？')
          .then(_ => {
              this.axios.post('user/deleteuser',row)
                    .then(res => {
                      console.log(res)
                          if (res.data) {
                            if(res.data){
                              //删除

                              this.$message.success(`删除成功`)
                            }
                            else{
                              //删除失败
                                  this.$message.error(`删除失败`);
                            }
                          }
                          this.reload();
                      })
                      .catch(err => {
                          this.$message.error(`用户无法删除`, 'ERROR!')
                      })
          })
          .catch(_ => {
          });
          
        },
        applysearch(){
          if(this.search =='' || this.radio ==''){
             this.$message.error(`搜索条件不能为空`);
             return;
          }
          let userData = {
              userId:'',
              userName:'',
          }; 
          switch(this.radio){
            case '1':
            userData.userId = this.search
            break;
            case '2':
            userData.userName = this.search
            break;
            default:
          }
           this.axios.post('user/usersearch',userData)
              .then(res => {
                    if (res.data) {
              
                      if(res.data.success!=false){
                        this.userList=res.data
                        
                      }
                      else{
                        this.$message.error(`未找到结果`);
                      }
                    }
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
        },
        reset(){
          this.handleUserList()
          this.search=''
          this.condition='1'
        },
          modify(row,index){
          this.$confirm('确认赋予管理员权限？')
          .then(_ => {
              this.axios.post('user/addadmin',row)
                    .then(res => {
                          if (res.data) {
                            if(res.data.success){
                              //权限赋予成功
                         
                              this.$message.success(`权限赋予成功成功`)
                            }
                            else{
                              //权限赋予失败
                                  this.$message.error(`权限赋予失败`);
                            }
                          }   
                      })
                      .catch(err => {
                          this.$message.error(`权限赋予失败`, 'ERROR!')
                      })
                      this.reload();
          })
          .catch(_ => {});
          
        },
        
    }
}
</script>

<style scoped>
  .readermanage{
    height: 500px;
  }
   .el-row {
    margin-bottom: 20px;
    margin-top: 20px;
   
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>


