<template>
    <div class="detail">
            <b-container>
  <!-- <b-tabs content-class="mt-3">
    <b-tab title="First" active><mylist></mylist></b-tab>
    <b-tab title="Second"><mylist></mylist></b-tab>
    <b-tab title="third" ><mylist></mylist></b-tab>
  </b-tabs> -->
  <b-card-group deck>
     <b-card
    no-body
    style="max-width: 30rem;margin:auto"
    img-src="https://placekitten.com/380/200"
    img-alt="Image"
    img-top

  >
    <h4 slot="header">借书卡</h4>
 <b-card-body>
      <b-card-title v-text="this.bookList.bookName"></b-card-title>
      <b-card-text>
        Some quick example text to build on the card title and make up the bulk of the card's
        content.
      </b-card-text>
    </b-card-body>
   

    <b-list-group flush>
      <b-list-group-item v-text="'作者：'+this.bookList.author"></b-list-group-item>
      <b-list-group-item v-text="'出版社：'+this.bookList.publisher"></b-list-group-item>
      <b-list-group-item v-text="'出借次数：'+this.bookList.borrowCount"></b-list-group-item>
        <b-list-group-item v-text="'库存：'+this.bookList.stockNumber"></b-list-group-item>
    </b-list-group>

    <b-card-body>
        <b-button variant="primary" @click="borrow" style="margin:20">我要借书</b-button>
      <b-button variant="success"  @click="addfavor" v-if="!isfavored">添加到收藏</b-button>
       <b-button variant="success"  @click="removefavor" v-else>取消收藏</b-button>
    </b-card-body>
  </b-card>
  

    </b-card-group>
</b-container>
    </div>            
</template>
<script>
import qs from 'qs';
export default {
        inject:['reload'],
  mounted(){
    
         this.axios.post('book/selectBookById',qs.stringify({bookId:this.$route.query.info})
        
        )   
              .then(res => {
          
                    if (res.data) {
                      console.log(res.data)  
                      this.bookList = res.data
                    }
                      else{
                        this.$message.error(`未找到结果`);
                      }
                    
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
          if(sessionStorage.getItem("user")  == null)
            {
                 this.isfavored = false;
                 return
            }
             let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
       this.axios.post('favor/query',qs.stringify({bookId:this.$route.query.info,readerId:session.userId})
        
        )   
              .then(res => {
                  console.log(res.data)
                    if (res.data) {
                    this.isfavored = true
                    this.favor = res.data
                    }
                      else{
                            this.isfavored = false;
                      }
                    
                })
                .catch(err => {
                    this.$message.error(`${err.message}`, 'ERROR!')
                })
  },
    data(){
    return{
       bookList:[],
       isfavored:false,
       favor:{},
    }
    },
    methods:{
       removefavor(){
                        this.axios
            .post('/favor/removefavor',qs.stringify({favorId:this.favor.favorId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 1) {
                    this.$message.success(`取消收藏成功`)
                    this.reload()
                          }
                          else{
                               this.$message.error(`error`, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
       },
      addfavor(){
            if(sessionStorage.getItem("user")  == null)
            {
                 this.$message.error(`请先登录`, 'ERROR!')
                 return
            }
             let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
              this.axios
            .post('/favor/addfavor',qs.stringify({readerId:session.userId,bookId:this.bookList.bookId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 1) {
                    this.$message.success(`添加成功`)
                    this.reload()
                          }
                          else{
                               this.$message.error(`添加失败`, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
      },
      borrow(){
            if(sessionStorage.getItem("user")  == null)
            {
                 this.$message.error(`请先登录`, 'ERROR!')
                 return
            }
            
            if(this.bookList.stockNumber == 0){
                 this.$message.error(`库存不足`, 'ERROR!')
                 return
            }
             let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
              this.axios
            .post('/borrow/borrowBook',qs.stringify({readerId:session.userId,bookId:this.bookList.bookId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 0) {
                    this.$message.success(`借书成功`)
                    this.reload()
                          }
                          else{
                               this.$message.error(`借书数量达到上限`, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
      }
    }
}
</script>
<style scoped>


</style>

