<template>
    <div class="borrowbook"  style="margin:auto">
    <b-container> 
      <el-row :gutter="20">
 <h4>我的借阅</h4>
</el-row>
     
      <el-table :data="myborrow.slice((currentPage-1)*pagesize,currentPage*pagesize)" border style=" width:100%"  max-height="600">
            <el-table-column prop="bookName" label="书名" width="180"> </el-table-column>
            <el-table-column prop="borrowDate" label="借书日期" width="180"> </el-table-column>
            <el-table-column prop="returnDate" label="归还日期" width="180"> </el-table-column>
            <el-table-column prop="renewCount" label="续借次数" width="180"> </el-table-column>
        <el-table-column label="操作">
           <template slot-scope="scope">
         <el-button-group v-if="scope.row.returnDate ==null">
     <el-button type="primary" round size="mini" @click="returnBook(scope.row)">还书</el-button>  
      <el-button type="danger" round  size="mini" @click="renewBook(scope.row)">续借</el-button>
      
        </el-button-group>
          <el-button-group v-else>
     <el-button type="primary" round size="mini" @click="returnBook(scope.row)">删除记录</el-button>  
      
      
        </el-button-group>
           </template> 
      </el-table-column>
        </el-table>

    
     
  <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="currentPage"
                            :page-sizes="[5, 10]"
                            :page-size="pagesize"        
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="myborrow.length">  
                    </el-pagination>
    
  

    </b-container>
       
    </div>
</template>
<script>
import { exists } from 'fs';
import qs from 'qs';
export default {
          inject:['reload'],
     mounted(){
  
        this.handlehisttory()
  },
     data(){
     return {
          currentPage:1, //初始页
                pagesize:10,    //    每页的数据
                myborrow:[],
      }
    },
    methods:{
              // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },
        handlehisttory() {
             if(sessionStorage.getItem('user')!=null){
        let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
         this.axios.post('borrow/getmyborrow',qs.stringify({readerId:session.userId})
        
        )   
              .then(res => {

                    if (res.data) {
                      console.log(res.data)  
                      this.myborrow = res.data
                    }
                      else{
                        this.$message.error(`未找到结果`);
                      }
                    
                })
                .catch(err => {
                    this.$message.error(`错误`, 'ERROR!')
                })
        
       }

        },
      returnBook(row){
                this.axios
            .post('/borrow/returnBook',qs.stringify({borrowId:row.borrowId,readerId:row.readerId,bookId:row.bookId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 0) {
                    this.$message.success(`还书成功`)
                    this.reload()
                          }
                          else{
                               this.$message.error(`还书失败`, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
      },

            renewBook(row){
                this.axios
            .post('/borrow/renewBook',qs.stringify({borrowId:row.borrowId,readerId:row.readerId,bookId:row.bookId}))
             .then(res => {
               console.log(res.data)
                          if (res.data == 0) {
                    this.$message.success(`续借成功`)
                    this.reload()
                          }
                          else{
                               this.$message.error(`续借次数到达上限`, 'ERROR!')
                          }
                      })
                      .catch(err => {
                          this.$message.error(`${err.message}`, 'ERROR!')
                      })
      }
       
        
    }
}
</script>

</script>

<style scoped>
  .borrowbook{
    height: 500px;
  }
   .el-row {
    margin-bottom: 20px;
    margin-top: 20px;
   
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>