<template>
  <div class="addbook">
    <el-form ref="bookList" :rules="addRules" :model="bookList" label-width="80px" size="small">
  <el-form-item label="图书名称" prop="bookName">
    <el-input v-model="bookList.bookName"></el-input>
  </el-form-item>
  <el-form-item label="ISBN"  prop="ISBN">
    <el-input v-model="bookList.ISBN"></el-input>
  </el-form-item>
  <el-form-item label="作者"  prop="author">
    <el-input v-model="bookList.author"></el-input>
  </el-form-item>
  <el-form-item label="出版社"  prop="publisher">
     <el-input v-model="bookList.publisher"></el-input>
  </el-form-item>
  <el-form-item label="类型"  prop="typeName">
     <el-select v-model="bookList.typeName" >
      <el-option label="教育" value="教育" checked></el-option>
      <el-option label="科幻" value="科幻"></el-option>
      <el-option label="自然" value="自然" checked></el-option>
      <el-option label="文学" value="文学"></el-option>
      <el-option label="政治" value="政治" checked></el-option>
      <el-option label="哲学" value="哲学"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item label="单价"  prop="price">
    <el-input v-model="bookList.price"></el-input>
  </el-form-item>
  <el-form-item  label="数量" prop="total">
    <el-input v-model="bookList.total"></el-input>
  </el-form-item>
    <el-form-item  label="简介" prop="description">
    <el-input v-model="bookList.description"></el-input>
  </el-form-item>
  <el-form-item  label="书架" prop="shelter">
    <el-input v-model="bookList.shelter"></el-input>
  </el-form-item>
   <el-form-item label="操作" width="180">
           <template >
       <el-upload
  class="upload-demo"
  ref="upload"
  name = "file"
  action="http://localhost:8085/book/postimg/"
  :on-change="handleChange"
   :beforeUpload="beforeAVatarUpload"
  :limit="limit"  
  :on-exceed = "onExceed"
   :on-remove="handleRemove"
 :onError="uploadError" 
 :onSuccess="uploadSuccess" 
 :auto-upload="false"
 :data = "bookList"
  :file-list="fileList">
  <el-button size="small" type="primary">点击上传</el-button>
  <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
</el-upload>
           </template> 
      </el-form-item >
  <el-form-item>
    <el-button type="primary" @click="onSubmit">确定</el-button>
    <el-button  @click="cancel">取消</el-button>
  </el-form-item>

  
</el-form>

  </div>
</template>
<script>
  export default {
    data() {
      
      var validateNull = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('不能为空'));
      } else {
        callback();
      }
      };
      var validateNumber = (rule, value, callback) => {
           var numReg = /^[0-9]+$/
        var numRe = new RegExp(numReg)

      if (value === '') {
        callback(new Error('不能为空'));
      }else if (!numRe.test(value)) {
        callback("必须是整数")
      
      } else {
        callback();
      }
      };
      var validateISBN = (rule, value, callback) => {
        var numReg = /^[0-9]+$/
        var numRe = new RegExp(numReg)


      if (value === '') {
        callback(new Error('不能为空'));
      } else if(value.length!=13 ){

        callback(new Error('必须为13位数字'));
      }
      else if (!numRe.test(value)) {
        callback("必须是数字")
      }
      else{
        callback();
      }
      };
      return {
          fileList:[
          ],
        limit: 1,
        bookList: {
                  bookName:'',
                  ISBN:'',
                  typeName:'',
                  author:'',
                  publisher:'',
                  price:'',
                  total:'',
                     description:'',
                  shelter:'',
        },
        addRules: {
        bookName: [
          { validator: validateNull, trigger: 'blur' }
        ],
        ISBN: [
          { validator: validateISBN, trigger: 'blur' }
        ],
        typeName: [
          { validator: validateNull, trigger: 'blur' }
        ],
        author: [
          { validator: validateNull, trigger: 'blur' }
        ],
        publisher: [
          { validator: validateNull, trigger: 'blur' }
        ],
        price: [
          { validator: validateNumber, trigger: 'blur' }
        ],
        total: [
          { validator: validateNumber, trigger: 'blur' }
        ],
        typeName: [
          { validator: validateNull, trigger: 'blur' }
        ]
      }
      }
    },
    methods: {
        submitUpload () {
        this.$refs.upload.submit()
      },
      handleChange(file, fileList) {
        this.fileList = fileList.slice(-3);
      },
        uploadSuccess (response, file, fileList) {
        if(response == 1){
          this.$message.success(`添加成功`)
        }
       else{
          this.$message.error(`增加失败`);
       }
      },
      uploadError (response, file, fileList) {
        this.$message.error('上传图片失败！')
      },
           onExceed (files, fileList) {
        this.$message.error('提示：只能上传一张图片！')
      },
      onSubmit() {
          this.$refs.bookList.validate((valid) => {
          if (valid) {  
            if(this.fileList.length !=0){
                this.submitUpload()
            }
            else{
                 this.$message.error(`图片为空`);
            }
            // console.log('submit!');
            // console.log(this.bookList)
            //   let bookData = {
            //     bookName: this.bookList.bookName,
            //     ISBN:this.bookList.ISBN,
            //     typeName:this.bookList.typeName,
            //     author:this.bookList.author,
            //     publisher:this.bookList.publisher,
            //     price:this.bookList.price,
            //     total:this.bookList.total,
            //     stockNum:this.bookList.total,
            //        description:this.bookList.description,
            //       shelter:this.bookList.shelter,
            //       picture:this.bookList.picture
            //   }; 
            //  this.axios.post('/book/insertbook',bookData)
            //   .then(res => {
            //         if (res.data) {
            //           console.log(res.data)
            //           if(res.data.success){
              
            //              this.$message.success(`增加成功`)
            //             this.bookList.bookName = ''
            //             this.bookList.ISBN = ''
            //             this.bookList.typeName =''
            //             this.bookList.author = ''
            //             this.bookList.publisher= ''
            //             this.bookList.price= ''
            //             this.bookList.total= ''
                        
                         
            //           }
            //           else{
            //                this.$message.error(`增加失败`);
            //           }
            //         }
            //     })
            //     .catch(err => {
            //         this.$message.error(`${err.message}`, 'ERROR!')
            //     })
          }
          
        })
      
      },
      cancel(){
        this.$router.push('/book')
      },
       beforeAVatarUpload (file) {
        if (file.type !== 'image/jpg' && file.type !== 'image/jpeg' && file.type !== 'image/png' && file.type !== 'image/gif') {
          this.$message.error('只支持jpg、png、gif格式的图片！')
          return false
        }
       },
         handleRemove (file, fileList) {
           this.fileList = []
        }
  
    }
  }
</script>
















<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .addbook{
    width: 50%;
    height: 500px;
    margin: auto;
  }
  .el-form{
    margin-top: 50px;
  }
</style>
