<template>
  <div class="mynavbar">
      <b-navbar toggleable="lg" type="dark" variant="info">
      <b-navbar-brand @click="gotohome">东华图书馆</b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav v-if="!isAdmin">
              <b-nav-item @click="gotomanager">查找所有图书</b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav v-else >
          <b-nav-item-dropdown text="图书管理" left>
            <b-dropdown-item @click="gotoaddbook">添加图书</b-dropdown-item>
            <b-dropdown-item @click="gotomanager">修改图书</b-dropdown-item>
            <b-dropdown-item @click="gototypemanager">类型管理</b-dropdown-item>
          </b-nav-item-dropdown> 
          <b-nav-item-dropdown text="用户管理" left>
            <b-dropdown-item @click="gotoborrowmanage">借阅管理</b-dropdown-item>
            <b-dropdown-item @click="gotoreadermanage">权限管理</b-dropdown-item>
          </b-nav-item-dropdown> 
        </b-navbar-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto" v-if="isLogin">
          <!-- <b-nav-form>
            <b-form-input size="sm" class="mr-sm-2" placeholder="Search"></b-form-input>
            <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
          </b-nav-form> -->
          <b-nav-item-dropdown right>
            <!-- Using 'button-content' slot -->
            <template slot="button-content"><em>{{userName}}</em></template>
            <b-dropdown-item @click="showreaderinfo">个人信息管理</b-dropdown-item>
            <b-dropdown-item @click="gotomyborrow">我的借阅</b-dropdown-item>
             <b-dropdown-item @click="gotomyfavor">我的收藏</b-dropdown-item>
               <b-dropdown-item @click="gotouserinfo">修改密码</b-dropdown-item>
          </b-nav-item-dropdown>
            <b-nav-item  @click="logout">退出登录</b-nav-item>
        </b-navbar-nav>
         <b-navbar-nav class="ml-auto" v-else>
          <!-- <b-nav-form>
            <b-form-input size="sm" class="mr-sm-2" placeholder="Search"></b-form-input>
            <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
          </b-nav-form>             -->
            <b-nav-item @click="showLoginModal">登录</b-nav-item>
            <b-nav-item @click="showRegisterModal">注册</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
      </b-navbar>
    <b-modal
      id="login"
      ref="loginmodal"
      title="用户登录"
      @show="resetModal"
      @hidden="resetModal"
      @ok="handleOk"
    >
      <form ref="loginform" @submit.stop.prevent="login">
        <b-form-group
          :state="nameState"
          label="学号"
          label-for="stuno"
          invalid-feedback="请输入九位学号"
        >
          <b-form-input
            ref="stunotext"
            id="stuno"
            v-model="stuno"
            :state="nameState"
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group
          :state="passwordstate"
          label="密码"
          label-for="password"
            
          invalid-feedback="请输入密码"
        >
          <b-form-input
            id="password"
            v-model="password"
                type = "password"
            :state="passwordstate"
            required
          ></b-form-input>
        </b-form-group>
      </form>

      
    </b-modal>

     <b-modal
      id="register"
      ref="registermodal"
      title="用户注册"
      @show="resetModal"
      @hidden="resetModal"
      @ok="handleregisterOk"
    >
      <form ref="registerform" @submit.stop.prevent="register">
        <b-form-group
          :state="registernameState"
          label="学号"
          label-for="stuno"
          invalid-feedback="请输入九位学号"
        >
          <b-form-input
            ref="registerstunotext"
            id="registerstuno"
            v-model="registerstuno"
            :state="registernameState"
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group
          :state="registerpasswordstate"
          label="密码"
          label-for="registerpassword"
          invalid-feedback="请输入密码"
        >
          <b-form-input
            id="registerpassword"
            type = "password"
            v-model="registerpassword"
            :state="registerpasswordstate"
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group
          :state="reregisterpasswordstate"
          label="确认密码"
          label-for="reregisterpassword"
          invalid-feedback="两次密码不一致"
        >
          <b-form-input
            id="reregisterpassword"
                type = "password"
            v-model="reregisterpassword"
            :state="reregisterpasswordstate"
            required
          ></b-form-input>
        </b-form-group>
      </form>

      
    </b-modal>

  </div>
</template>

<script>

import qs from 'qs';
export default {
      inject:['reload'],
  name: 'mynavbar',
   data() {
      return {
        userName:'',
        isLogin:false,
         stuno: '',
        nameState: null,
          password: '',
        passwordstate: null,
            registerstuno: '',
        registernameState: null,
          registerpassword: '',
        registerpasswordstate: null,
          reregisterpassword: '',
        reregisterpasswordstate: null,
        isAdmin:false,
      }
  },
   mounted() { 
      console.log(sessionStorage.getItem('user'))
       if(sessionStorage.getItem('user')!=null){
        let m = sessionStorage.getItem("user")
        let session = JSON.parse(m);
         this.isLogin = true;
         this.userName = session.userName;
          if(session.authority == 99){
             this.isAdmin = true;
          }
       }

       
  },
  methods:{
      gototypemanager(){  this.$router.push('/typemanage');},
    gotoreadermanage(){  this.$router.push('/readermanage');},
    gotoborrowmanage(){  this.$router.push('/borrowmanage');},
    gotouserinfo(){
        this.$router.push('/userinfo');
    },
    gotoaddbook(){
         this.$router.push('/addbook');
    },
      gotomyfavor(){
   this.$router.push('/myfavor');
    },
    gotomyborrow(){
   this.$router.push('/myborrow');
    },
      gotomanager(){
         this.$router.push('/manager');
      },
      showreaderinfo(){
  this.$router.push('/readerinfo');
      },
       gotohome(){
         this.$router.push('/');
      },
    //显示登录窗口
     showLoginModal() {
        this.$refs['loginmodal'].show()
      },
      showRegisterModal(){
        this.$refs['registermodal'].show()
      },
      checkRegisterFormValidity() {
       var valid = true;
        if(this.registerstuno == '' || this.registerstuno.length !=9){
           this.registernameState = 'invalid'
           valid = false;
        }
        else{
          this.registernameState = 'valid'
        }
        if(this.registerpassword ==''){
          this.registerpasswordstate = 'invalid'
          valid = false;
        }
        else{
          this.registerpasswordstate = 'valid'
        }
        if(this.reregisterpassword =='' || this.reregisterpassword != this.registerpassword){
          this.reregisterpasswordstate = 'invalid'
          valid = false;
        }
        else{
          this.reregisterpasswordstate = 'valid'
        }
        console.log(valid)
        return valid
      },
      //验证登录窗口
     checkLoginFormValidity() {
       var valid = true;
        if(this.stuno == '' || this.stuno.length !=9){
           this.nameState = 'invalid'
           valid = false;
        }
        else{
          this.nameState = 'valid'
        }
        if(this.password ==''){
          this.passwordstate = 'invalid'
          valid = false;
        }
        else{
          this.passwordstate = 'valid'
        }
        
        console.log(valid)
        return valid
      },
      resetModal() {
        this.stuno = ''
        this.password = ''
        this.nameState = null
        this.passwordstate = null
        this.registerstuno = ''
        this.registerpassword = ''
        this.registernameState = null
        this.registerpasswordstate = null
        this.reregisterpassword = ''
        this.reregisterpasswordstate = null
      },
      handleOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.handleSubmit()
      },
      handleregisterOk(bvModalEvt) {
        // Prevent modal from closing
        bvModalEvt.preventDefault()
        // Trigger submit handler
        this.handleregisterSubmit()
      },
      handleregisterSubmit() {
      if (!this.checkRegisterFormValidity()) {
          return
        }
        //提交注册信息
          // Hide the modal manually
              this.axios.post('/user/signup',qs.stringify({userName:this.registerstuno,password:this.registerpassword}))
        .then(res => {
          console.dir(res.data)
                    if (res.data != null) {
                    //注册成功
                
                    
                      this.$message.success(`注册成功`)
                      // 登录成功 跳转至首页
                  
                    }else{
                      this.$message.error(`注册失败，用户名已存在`);
                      return false;
                    }
                })
                .catch(err => {
                    console.log(err)
                })
              
        this.$nextTick(() => {
          this.$refs.registermodal.hide()
        })
        },
       logout(){
          this.$confirm('确认退出？')
          .then(_ => {
             sessionStorage.removeItem('user');
            this.$message.success(`退出成功`)
         this.isLogin = false;
         this.isAdmin = false;
         this.$router.push('/')
          })
          .catch(_ => {});
      
     
      },
      handleSubmit() {
        // Exit when the form isn't valid
        if (!this.checkLoginFormValidity()) {
          return
        }
        let formData = {
            stuno: this.stuno,
            password: this.password,
        }; 
        // 提交登录请求
        this.axios.post('/user/login',qs.stringify(formData))
        .then(res => {
          console.dir(res.data)
                    if (res.data) {
                      //登陆成功
                      sessionStorage.setItem('user',JSON.stringify(res.data));
                      //this.userLogin(res.data);
                      this.$message.success(`登陆成功`)
                      // 登录成功 跳转至首页
                   this.reload()

                     
                      this.userName = res.data.userName
                      this.isLogin = true;
                      if(res.data.authority == 99){
                        this.isAdmin = true;
                      }
                    }else{
                      this.$message.error(`登录失败`);
                      return false;
                    }
                })
                .catch(err => {
                    console.log(err)
                })
              
        // Hide the modal manually
        this.$nextTick(() => {
          this.$refs.loginmodal.hide()
        })
      }
  }
}
</script>

<style scoped>
  b-row{
    margin-bottom: 20px;
  }
.Verification{
    width: 160px;
    height: 40px;
    background: #ccc;
    float: left;
    margin-left: 20px;
  }
</style>
